<template>
  <view class="almost-lottery">
	<navbar :iSimmersive="true" title="抽奖活动"></navbar>
    
	<view v-if="!isLoading">
		<view class="almost-lottery__head">
			<image v-if="static" :src="static+'app/top-bg.png'"></image>
			<view class="tip" v-if="tips.length > 0">
				<swiper class="swiper" :indicator-dots="false" :autoplay="true" :vertical="true" :circular="true">
					<swiper-item v-for="item in tips">
						<text class="tip-content">{{item.content||""}}</text>
					</swiper-item>
				</swiper>
			</view>
		</view>
		
		<view class="almost-lottery__wheel">
		  <almost-lottery
			:canvasId="canvasConfig.canvasId"
			:canvasWidth="canvasConfig.width"
			:canvasHeight="canvasConfig.height"
			:ringCount="2"
			:duration="1"
			:prizeList="prizeList"
			:prizeIndex="prizeIndex"
			@reset-index="prizeIndex = -1"
			@draw-start="handleDrawStart"
			@draw-end="handleDrawEnd"
			@finish="handleDrawFinish"
			v-if="prizeList.length"
		  />
		  <view class="count">
			<text class="text">剩余抽奖次数：{{ freeNum }} 次</text>
			<text class="point">积分：{{goldPoint}}</text>
		  </view>
		</view>
		
		<view class="almost-lottery_rule clear">
			<view class="rule-title">规则说明</view>
			<view class="rule-content">
				<view v-for="item in content">{{item}}</view>
			</view>
		</view>
		<view class="almost-lottery_rule_footer"></view>
	</view>
	<loading v-if="isLoading"></loading>
	<authorize v-model="isAuthShow"></authorize>
  </view>
</template>

<script>
	import AlmostLottery from '@/uni_modules/almost-lottery/components/almost-lottery/almost-lottery.vue'
	import { clearCacheFile } from '@/uni_modules/almost-lottery/utils/almost-utils.js'
	import navbar from "@/components/navbar/navbar";
	import loading from '../../components/tool/loading'
	export default {
		name: 'AlmostLotterys',
		components: {
			AlmostLottery,navbar,loading
		},
	data () {
		return {
			isLoading:true,
			isAuthShow: false,
			static: "",
			// 以下是画板配置相关数据
			// canvas id、宽、高
			canvasConfig: {
			  canvasId: 'almostLotteryCanvas',
			  width: 280,
			  height: 280
			},
			
			// 以下是奖品配置数据
			// 奖品数据
			prizeList: [],
			// 中奖下标
			prizeIndex: -1,

			// 是否正在抽奖中，避免重复触发
			prizeing: false,

			// 以下为业务需求有关示例数据
			// 积分余额
			goldPoint: 600,
			// 当日抽奖次数余额
			freeNum: 3,
			// 每次消耗的金币数
			goldNum: 20,
			content: "",
			result: null,
			tips:[]
		}
	},
	onLoad () {
		this.static = this.$static;
		this.getPrizeList();
	},
	onUnload () {
		uni.hideLoading()
	},
	methods: {
		// 获取奖品列表
		async getPrizeList () {
			this.$http.getActivityData().then(res=>{
				if(res.status == 1){
					this.freeNum = res.data.count;
					this.goldPoint = res.data.pointTotal;
					this.goldNum = res.data.point;
					this.content = res.data.content;
					this.prizeList = res.data.data;
					this.tips = res.data.tips;
					if(this.content != ''){
						let array = this.content.split(/\<p\>(.*)\<\/p\>/);
						let result = [];
						for(let i=0; i<array.length - 1; i++){
							if(array[i] != '' && array[i] != '\n'){
								result.push(array[i]);
							}
							
						}
						
						if(uni.getSystemInfoSync().platform != 'ios'){
							result.pop();
						}
						
						this.content = result;
					}
					
				}else{
					this.$utils.msg("服务器无响应，请稍后在试")
				}
				this.isLoading = false;
			}).catch(err=>{
				this.isLoading = false;
			});
		},
		// 本次抽奖开始
		handleDrawStart () {
			if (this.prizeing) return
			this.prizeing = true

			this.$store.dispatch("usersStatus").then(()=>{
				this.isAuthShow = false;
				if (this.freeNum > 0 && this.goldPoint >= this.goldNum) {
					this.$http.getActivityPrize().then(res=>{
						if(res.status){
							let list = [...this.prizeList]
							for (let i = 0; i < list.length; i++) {
								let item = list[i]
								if (item.prizeId === res.data.prizeId) {
									// 中奖下标
									this.prizeIndex = i
									this.result = res.data.res;
									break
								}
							}
						}else{
							this.$utils.msg(res.info);
						}
					}).catch(err=>{
						this.$utils.msg("服务器无响应，请稍后在试");
					});
				} else {
					this.prizeing = false
					if(this.freeNum <= 0){
						this.$utils.msg("您的抽奖次数己用完！");
					}else{
						uni.showModal({
							title: '积分不足',
							content: '您的积分不足？',
							success: (res) => {}
						})
					}
				}
			}).catch(()=>{
				this.prizeing = false;
				this.isAuthShow = true;
			});
		},
		// 本次抽奖结束
		handleDrawEnd () {
			this.freeNum--;
			if (this.goldPoint >= this.goldNum) this.goldPoint -= this.goldNum
			
			// 旋转结束后，开始处理拿到结果后的逻辑
			this.prizeing = false
			let prizeName = this.prizeList[this.prizeIndex].name
			let tipContent = ''

			// if (prizeName === '谢谢参与') {
			// 	tipContent = '很遗憾，没有中奖，请再接再厉！'
			// } else {
			// 	// 如果奖品设有库存
			// 	if (this.onStock) {
			// 		let prizeStock = this.prizeList[this.prizeIndex].stock
			// 		tipContent = prizeStock ? `恭喜您，获得 ${prizeName}` : `很抱歉，您来晚了，当前奖品 ${prizeName} 已无库存`
			// 	} else {
			// 		tipContent = `恭喜您，获得 ${prizeName} ！`
			// 	}
			// }
			
			uni.showModal({
				content: this.result.description,
				showCancel: false
			})
		},
		// 抽奖转盘绘制完成
		handleDrawFinish (res) {}
    }
  }
</script>

